import { NgbDate } from '@ng-bootstrap/ng-bootstrap';
import * as moment from 'moment';

export function ngbDateToMoment(date: NgbDate): moment.Moment {
  return moment().year(date.year).month(date.month - 1).date(date.day);
}

export function momentToNgbDate(date: moment.Moment): NgbDate {
  return new NgbDate(date.year(), date.month() + 1, date.date());
}
