import { AfterViewInit, Component, forwardRef, OnInit } from '@angular/core';
import { ControlValueAccessor, FormControl, NG_VALUE_ACCESSOR } from '@angular/forms';
import { faCalendarAlt } from '@fortawesome/free-regular-svg-icons';
import { NgbCalendar, NgbDate, NgbDateParserFormatter } from '@ng-bootstrap/ng-bootstrap';
import { momentToNgbDate, ngbDateToMoment } from '@shared/functions/date.util';
import * as moment from 'moment';

import { DatePickerRangeModel } from './date-picker-range.model';

const CUSTOM_DATE = -1;
const ALL_TIME = -2;

type Presets = 'week' | 'month' | 'quarter' | 'year';

@Component({
  selector: 'duelit-date-picker-range',
  templateUrl: './date-picker-range.component.html',
  styleUrls: ['./date-picker-range.component.scss'],
  providers: [{
    provide: NG_VALUE_ACCESSOR,
    useExisting: forwardRef(() => DatePickerRangeComponent),
    multi: true
  }]
})
export class DatePickerRangeComponent implements OnInit, ControlValueAccessor, AfterViewInit {
  readonly allTimeMinDate = momentToNgbDate( moment('2010-01-01') );
  readonly preSelectedDates: {
    label: string,
    value: number | Presets,
    hidden?: boolean
  }[] = [{
    label: 'Today',
    value: 0
  }, {
    label: 'This week',
    value: 'week'
  }, {
    label: 'This month',
    value: 'month'
  }, {
    label: 'This quarter',
    value: 'quarter'
  }, {
    label: 'This year',
    value: 'year'
  }, {
    label: '2 days ago',
    value: 1
  }, {
    label: '3 days ago',
    value: 2
  }, {
    label: '5 days ago',
    value: 4
  }, {
    label: '7 days ago',
    value: 6
  }, {
    label: 'All time',
    value: ALL_TIME
  }, {
    label: 'Custom',
    value: CUSTOM_DATE,
    hidden: true,
  }];

  preSelectedDate = new FormControl(-1);

  maxDate: NgbDate | null = null;
  minDate: NgbDate | null = null;
  calendarIcon = faCalendarAlt;

  hoveredDate: NgbDate | null = null;

  private _from: NgbDate;
  private _to: NgbDate;

  onChange: any = () => {};
  onTouch: any = () => {};

  constructor(
    private calendar: NgbCalendar,
    public formatter: NgbDateParserFormatter
  ) {
    this.maxDate = calendar.getToday();
    this.minDate = calendar.getToday();
    this.minDate.year = 2010;
    this.minDate.month = 1;
    this.minDate.day = 1;
    // this.toDate = calendar.getToday();
    // this.fromDate = calendar.getPrev(calendar.getToday(), 'd', 6);
  }

  get fromDate(): NgbDate {
    return this._from;
  }
  set fromDate(date: NgbDate) {
    if (date && !date.equals(this._from)) {
      setTimeout(() => { this.preSelectedDate.setValue(CUSTOM_DATE); }, 0);
    }
    this._from = date;
    this.onChange({
      from: date,
      to: this.toDate
    });
  }

  get toDate(): NgbDate {
    return this._to;
  }
  set toDate(date: NgbDate) {
    if (date && !date.equals(this._to)) {
      setTimeout(() => { this.preSelectedDate.setValue(CUSTOM_DATE); }, 0);
    }
    this._to = date;
    this.onChange({
      from: this.fromDate,
      to: date
    });
  }

  ngOnInit(): void {
  }

  ngAfterViewInit(): void {
    setTimeout(() => this.setInitValueOfPresetDate(), 0);
  }

  onDateSelection(date: NgbDate) {
    if (!this.fromDate && !this.toDate) {
      this.fromDate = date;
    } else if (this.fromDate && !this.toDate && date.after(this.fromDate)) {
      this.toDate = date;
    } else {
      this.toDate = null;
      this.fromDate = date;
    }
    this.preSelectedDate.setValue(CUSTOM_DATE);
  }

  isHovered(date: NgbDate) {
    return this.fromDate && !this.toDate && this.hoveredDate && date.after(this.fromDate) && date.before(this.hoveredDate);
  }

  isInside(date: NgbDate) {
    return this.toDate && date.after(this.fromDate) && date.before(this.toDate);
  }

  isRange(date: NgbDate) {
    return date.equals(this.fromDate) || (this.toDate && date.equals(this.toDate)) || this.isInside(date) || this.isHovered(date);
  }

  isDisabled(date: NgbDate) {
    return date.after(this.maxDate)
      || date.before(this.minDate);
  }

  validateInput(currentValue: NgbDate | null, input: string): NgbDate | null {
    const parsed = this.formatter.parse(input);
    return parsed && this.calendar.isValid(NgbDate.from(parsed)) ? NgbDate.from(parsed) : currentValue;
  }

  writeValue(value: DatePickerRangeModel) {
    this.fromDate = value.from;
    this.toDate = value.to;
  }

  registerOnChange(fn: any) { this.onChange = fn; }

  registerOnTouched(fn: any) { this.onTouch = fn; }

  setPreSelectedDate(value: number|Presets) {
    this.preSelectedDate.setValue(value);
    this._to = momentToNgbDate(moment());

    if (value === ALL_TIME) {
      this._from = this.allTimeMinDate;
    } else {
      if (typeof(value) === 'number') {
        this._from = momentToNgbDate(moment().subtract(value, 'days'));
      } else {
        this._from = momentToNgbDate(moment().startOf(value));
      }
    }

    this.onChange({
      from: this.fromDate,
      to: this.toDate
    });
  }

  getPreSelectedDateLabel(): string {
    return this.preSelectedDates.find(item => item.value === this.preSelectedDate.value).label;
  }

  isPreSelectedItemSelected(item: { value: number }): boolean {
    return this.preSelectedDate.value  === item.value;
  }

  setInitValueOfPresetDate() {
    const from = ngbDateToMoment(this.fromDate);
    const to = ngbDateToMoment(this.toDate);

    const diff = to.diff(from, 'days');

    const presetIndex = this.preSelectedDates.findIndex(preset => preset.value === diff);

    if (presetIndex >= 0) {
      this.preSelectedDate.setValue(diff);
      return;
    }

    ['year', 'quarter', 'month', 'week'].forEach((preset: Presets) => {
      if (to.diff(moment().startOf(preset), 'days') === diff) {
        this.preSelectedDate.setValue(preset);
      }
    });
  }
}
