export enum TransactionType {
  Add = 'add',
  AdminCredits = 'adminCredits',
  Transaction = 'transaction',
  RedeemCoinsRequest = 'redeemCoinsRequest'
}

export function isTransactionTypeValid(type: string) {
  return Object.keys(TransactionType).findIndex(key => {
    return TransactionType[key] === type;
  }) >= 0;
}
